import { sqliteTable, text, integer, real } from 'drizzle-orm/sqlite-core'

export const users = sqliteTable('users', {
  // Basic Information
  id: integer('id').primaryKey({ autoIncrement: true }),
  username: text('username').notNull().unique(), // Username
  email: text('email').notNull().unique(), // Email
  
  // Authentication
  passwordHash: text('password_hash').notNull(), // Password hash
  salt: text('salt').notNull(), // Password salt
  
  // Personal Information
  firstName: text('first_name'), // First name
  lastName: text('last_name'), // Last name  
  displayName: text('display_name'), // Display name
  gender: text('gender'), // Gender: male, female, other
  avatar: text('avatar'), // Avatar URL
  bio: text('bio'), // Biography
  
  // Contact Information
  phone: text('phone'), // Phone number
  address: text('address'), // Address
  
  // Account Status
  status: text('status').notNull().default('active'), // active, inactive, suspended, deleted
  emailVerified: integer('email_verified', { mode: 'boolean' }).notNull().default(false), // Is email verified
  phoneVerified: integer('phone_verified', { mode: 'boolean' }).notNull().default(false), // Is phone verified
  
  // Permissions and Roles
  role: text('role').notNull().default('user'), // user, admin, moderator, etc.
  permissions: text('permissions'), // JSON formatted list of permissions
  
  // Preferences
  language: text('language').default('zh-CN'), // Language preference
  timezone: text('timezone').default('Asia/Shanghai'), // Timezone
  theme: text('theme').default('light'), // Theme preference: light, dark, auto
  
  // Login Information
  lastLoginAt: integer('last_login_at', { mode: 'timestamp' }), // Last login timestamp
  lastLoginIp: text('last_login_ip'), // Last login IP
  loginCount: integer('login_count').notNull().default(0), // Login count
  
  // Security
  twoFactorEnabled: integer('two_factor_enabled', { mode: 'boolean' }).notNull().default(false), // Is two-factor authentication enabled
  twoFactorSecret: text('two_factor_secret'), // Two-factor authentication secret
  recoveryToken: text('recovery_token'), // Account recovery token
  recoveryTokenExpiresAt: integer('recovery_token_expires_at', { mode: 'timestamp' }), // Recovery token expiration timestamp
  
  // Timestamps
  createdAt: integer('created_at', { mode: 'timestamp' }).notNull().$defaultFn(() => new Date()),
  updatedAt: integer('updated_at', { mode: 'timestamp' }).notNull().$defaultFn(() => new Date()),
  deletedAt: integer('deleted_at', { mode: 'timestamp' }), // Soft delete timestamp
})

