import type { SuccessResponse, ErrorResponse } from '../types/response.type'


export const successResponse = (message: string, data: any): SuccessResponse => {
    return {
        code: 200,
        message: message,
        data: data
    }
}

export const errorResponse = (message: string, data: any): ErrorResponse => {
    return {
        code: 500,
        message: message,
        data: data
    }
}